.Tarif {background-color: #FFFFFF; color: black; padding:60px 120px 60px 120px;}

.Tarif .title {font-size: 50px; margin-left:10px; color: #E55039; letter-spacing: 2px; margin:0px 30px 30px 30px; font-weight:bold; height: auto;}

.Tarif .container-boxs {width:100%; height:auto; display: flex; flex-wrap: wrap; gap:50px; justify-content: space-between; }

    .Tarif .box {}
        .Tarif .title-box {color: #E55039; font-size:18px; margin-bottom: 20px; justify-content: center; display: flex; font-weight:bold;}
        .Tarif .background-box {position:relative; width:380px; height:580px; padding:40px 20px; border-radius: 20px; box-sizing: border-box; overflow: hidden; justify-content:space-between; display:flex; flex-direction: column; align-items: center;} 
            .Tarif .background-img {position:absolute; top:0px; z-index:9; height:580px; width:auto}

            .Tarif .container-price {position: relative; z-index:10; justify-content: center; display:flex; align-items: center; background-color: white; width:245px; height:50px; border-radius: 5px;}
                .Tarif .price {font-size: 25px}
            .Tarif .button-add {position: relative; z-index:10; justify-content: center; display:flex; align-items: center; background-color: #E55039; color:white; font-size: 15px; font-weight:bold; letter-spacing:1px; width:245px; height:50px; border-radius: 25px; cursor:pointer}


@media screen and (max-width:1024px) {
    .Tarif .container-boxs {
        justify-content: center;
    }

    .Tarif .title {margin:0 0 30px 0; display: flex; justify-content: center;}
}

@media screen and (max-width: 768px) {
    .Bienvenue .title {
        font-size: 50px;
        flex-direction: column;
    }

    .Bienvenue .subtitle {
        font-size: 20px;
    }

    .Bienvenue .the, .Bienvenue .french {
        margin-right: 0px;
    }

}

@media screen and (max-width: 480px) {
    .Tarif .background-box {width:300px; height:480px;} 

    .Tarif .title {font-size: 35px;}
}